.nav-link{
    color: white;
}
.nav-item a:hover::after{
    content: "";
    /* color: white; */
    background-color: red;
    position: absolute;
    bottom: 0;
    height: 2px;
    width: 100%;
    /* border-bottom: 3px solid var(--primary-color); */
    left: 0;

}


.c-navbar .nav-link{
    color: white;
}
.nav-item a{
  transition: 0.3s all;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
  padding-left: 10px;
  position: relative;
}
.is-sticky {
    background-color: white;
    height: 75px;
    width: 100%;
    top: 0;
    position: sticky;
    z-index: 12121212;
    transition: 0.3s all;

  }
 
  .rr{
    
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
    /* background-color: red !important; */
    animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
    padding-top: 0px;
    padding-bottom: 0px;
    transition: 0.3s all;
   
  }
  .nav-link{
    color: white;
  }
  .rr .nav-link {
    color: white!important;
}
.is-sticky .nav-link {
    color: black!important;
}




